<template>
  <div>
    <SystemBar></SystemBar>
    <AppBar></AppBar>
    <HomeSection
      v-bind:style="{ 'min-height': screenHeightAvailable }"
      class="pt-12"
    />
    <AboutSection
      v-bind:style="{ 'min-height': screenHeightAvailable }"
    />
    <StepByStepSection
      v-bind:style="{ 'min-height': screenHeightAvailable }"
    />
  </div>
</template>

<script>
import SystemBar from "@/components/SystemBar.vue";
import AppBar from "@/components/AppBar.vue";
import HomeSection from "./home-section/HomeSection.vue";
import AboutSection from "./about-section/AboutSection.vue";
import StepByStepSection from "./step-by-step-section/StepByStepSection.vue";

export default {
  name: 'LandingPage',

  components: {
    SystemBar,
    AppBar,
    HomeSection,
    AboutSection,
    StepByStepSection
  },

  computed: {
    screenHeightAvailable: function () {
      const vh = Math.max(document.documentElement.clientHeight || 0, window.innerHeight || 0);
      return `${vh - (this.$vuetify.application.bar + this.$vuetify.application.top)}px`;
    }
  }

}
</script>
