<template>
  <v-dialog
    max-width="70vw"
    v-bind="$attrs"
    :value="value"
    @input="emitValueUpdate"
  >
    <div class="youtube-container primary" v-if="value">
      <iframe class="youtube-iframe" :src="'https://www.youtube-nocookie.com/embed/'+videoid" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
    </div>
  </v-dialog>
</template>

<script>
/*
  Props:
  #value -> v-model do componente
  #videoid -> Id do video do youtube. Ex: youtube.com/watch?v=HciKU63dLtA -> id=HciKU63dLtA
*/
export default {
  name: 'YouTubeDialog',

  props: {
    value: Boolean,
    videoid: String,
  },

  methods: {
    emitValueUpdate(newValue) {
      this.$emit('input', newValue)
    }
  }
}
</script>

<style lang="scss" scoped>
.youtube-container {
  position: relative;
  overflow: hidden;
  width: 100%;
  padding-top: 56.25%; /* 16:9 Aspect Ratio (9/16 = 0.5625) */
}

.youtube-iframe {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
</style>
